// import axios from "axios";
import axios from "../api/request.js";
const baseUrl = process.env.BASE_API;

const urls = {
  table: '/app/service_department/list.htm',
  comments: '/app/service_department/comment_list.htm',
  details: '/app/order/delivering_details.htm',
  reject: '/app/order/reject.htm?WX_TYPE=OfficialAccount'
}

// 
const apis = Object.keys(urls)
  .reduce((acc, url) => {
    acc[url] = baseUrl + urls[url]
    return acc
  }, {});
  
// 
export const getComments = () => {
  return axios({
    url: apis.comments,
    method: 'get',
    params: axios.getData({})
  });
};

// 
export const getAllOrder = () => {
  return axios({
    url: apis.table,
    method: 'get',
    params: axios.getData({})
  });
};

// 
export const getOrder = (val,status) => {
  return axios({
    url: apis.table,
    method: 'get',
    params: axios.getData({orderStatus:val, orderStatus:status})
  });
};

//  
export const getDealing = () => {
  return axios({
    url: apis.table,
    method: 'get',
    params: {
      WX_TYPE: "OfficialAccount",
      orderStatus: 'OnDelivery'
    }
  })
}

//  
export const getUnSettle = () => {
  return axios({
    url: apis.table,
    method: 'get',
    params: {
      WX_TYPE: "OfficialAccount",
      orderStatus: 'UnSettle'
    }
  })
}

// 
export const rejectOrder = (param) => {
  return axios({
    url: apis.reject,
    method: 'post',
    data: axios.postData(param)
  });
};




