<template>
  <div class="search-page">
    <page-content>
      <form action="/">
        <van-search placeholder="" v-model="searchKey" @search="search"/>
      </form>
      <div class="search-grids">
        <el-table :data="tableData"
                border
                :header-cell-style='styleObj'
                style="width: 100%">
        <el-table-column label="">
          <template slot-scope="scope">
            <span>{{scope.row.memberDistrict}}{{scope.row.memberAddress}}{{scope.row.memberRoom}}</span>
          </template>               
        </el-table-column>
        <el-table-column label=""
                         width="90"
                         align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.orderStatus == 'Collaging'" style="color: green; font-size:14px"></span>
            <span v-if="scope.row.orderStatus == 'OnDelivery'" style="color: green; font-size:14px"></span>
            <span v-if="scope.row.orderStatus == 'HoldDelivery'" style="color: red; font-size:14px" ></span>
            <span v-if="scope.row.orderStatus == 'UnSettle'" style="color: red; font-size:14px" ></span>
            <span v-if="scope.row.orderStatus == 'UnDeal'" style="color: red; font-size:14px" ></span>
            <span v-if="scope.row.orderStatus == 'Refuse'" style="color: red; font-size:14px" ></span>
            <span v-if="scope.row.orderStatus == 'completed'" style="color: green; font-size:14px"></span>
            <span v-if="scope.row.orderStatus == 'cancelled'" style="color: red; font-size:14px" ></span>
            <span v-if="scope.row.orderStatus == 'Finish'" style="color: green; font-size:14px"></span>
            <span v-if="scope.row.orderStatus == 'Closed'" style="color: red; font-size:14px" ></span>
          </template>
        </el-table-column>
        <el-table-column label=""
                         width="90"
                         align="center">
          <template slot-scope="scope">
            <div v-if="scope.row.orderStatus == 'UnSettle'"  class="tableWrap">
              <span  @click="handleCancle(scope.row.sn)" style="color: red; font-size:14px"></span>
              <span @click="handleAccept(scope.row.sn)" style="color: green; font-size:14px"></span>
            </div>
            <span v-else @click="handleCheck(scope.row.sn)" style="color: green; font-size:14px"><i style="font-size:16px" class="iconfont icon-065chakandingdan"></i></span>
          </template>
        </el-table-column>
      </el-table>
      </div>
      <p v-if="tableData !== null" class="search-button"><a @click="next" ></a></p>
    </page-content>
  </div>
</template>
<script>
import Vue from 'vue'
import Content from '../components/content'
import Search from '../../node_modules/vant/lib/search';
import '../../node_modules/vant/lib/search/style';
import Toast from '../../node_modules/vant/lib/toast';
import '../../node_modules/vant/lib/toast/style';
import { handleLogin } from "@/api/login.js";
import { getAllOrder, searchOrder, rejectOrder } from '@/api/search.js'
import _ from 'lodash'; //lodash
import axios from 'axios' //axios

//canceltoken
let sources = [];
Vue.use(Search);
Vue.use(Toast)
export default {
  components: {
    'page-content': Content
  },
  data () {
    return {
      styleObj: {'background': '#F2F2F2'},
      tableData: [],
      listData: {},
      searchKey: '',
      pageNumber: 1
    }
  },
  methods: {
    //
    search: _.debounce(
      function () {
        let that = this;
        _.remove(sources, function (n) {
          return n.source === null;
        });
        sources.forEach(function (item) {
          if (item !== null && item.source !== null && item.status === 1) {
            item.status = 0;
            item.source.cancel('')
          }
        });
        var sc = {
          source: axios.CancelToken.source(),
          status: 1 //10:
        };
        sources.push(sc);
        axios.get('/api/app/service_department/search.htm', {
          cancelToken: sc.source.token,
          params: {
            keyword: this.searchKey,
            WX_TYPE: 'OfficialAccount'
          }
        }).then(function (res) {
          //
          sc.source = null; //canceltoken
          console.log('');
          console.log('',res.data);
          if( res.data.data != null ){
            that.tableData = res.data.data.content
            that.listData = res.data.data
            console.log('');
          } else {
            that.tableData = null
            console.log('');
          }
        }).catch(() => { 
          console.log('');
          sc.source = null; //canceltoken
        })
      },
      500 //500ms
    ),
    handleCancle(id) {
      this.$dialog.confirm({
        title: ''
      }).then(() => {
        let formdata = new FormData()
        formdata.append('sn', id)
        rejectOrder(formdata).then(res => {
          console.log('order cancled');
        }).then(this.$router.go(0))
      }).catch(() => {
        console.log('u cancled');
      });
    },
    handleAccept(id) {
      this.$router.push({path:'/AcceptOrder',query:{sn:id}})
    },
    handleCheck(id) {
      this.$router.push({path:'/checkout',query:{sn:id}})
    },
    next() {
      if (this.searchKey == '') {
        this.pageNumber ++
        searchOrder(this.pageNumber).then(res => {
          if (res.data.data != null) {
            this.tableData = this.tableData.concat(res.data.data.content)
            Toast.success('');
          } else {
            console.log('');
            Toast.fail('');
          }
        })
      } else {
        this.pageNumber ++
        searchOrder(this.pageNumber, this.searchKey).then(res => {
          console.log(res);
          if (res.data.data != null) {
            this.tableData = this.tableData.concat(res.data.data.content)
          } else {
            console.log('');
            Toast.fail('');
          }
        })
      }
    }
  },
  mounted () {
    handleLogin().then((res) => {
      getAllOrder().then((res) => {
        this.tableData = res.data.data.content
        this.listData = res.data.data
        console.log(res.data);
      });
    })
  }
}

</script>
<style lang="less" scoped>
  .search-page{
    font-size: .75rem;
  }
  .search-grids {
    // margin:.6rem .6rem 4rem; 
    font-size: 12px;
    margin: .425rem;
    // font-size: .75rem;
  }
  .search-load{
    text-align: center;
  }
  .search-button {
    text-align: center;
    margin-bottom: 3.5rem;
    a {
      text-decoration:none;
      color:rgb(122, 121, 121); 
    }
  }
</style>



