<template>
  <div class="page">
    <page-content>
      <div class="comment-courierList">
        <p></p>
        <!-- <select class="comment-courierList__selectbox" v-model="selected">
          <option v-for="item in couriers" :key="item.value" >{{item.text}}</option>
        </select> -->
        <select @change="handleSelect(id)" class="comment-courierList__selectbox" v-model="id" style="font-size:14px">
          <option v-for="item in couriers" :key="item.name" :value="item.id" >{{item.name}}</option>
        </select>
      </div>
      <div v-for="item in billLists" :key="item.index" class="orderDetail-list__detail">
        <div class="orderDetail-list__total">
          <p><span class="fontBold">:&nbsp;&nbsp;</span>{{filterDate(item.departmentDate)}}</p>
          <!-- <p><span class="fontBold">:&nbsp;&nbsp;</span>{{item.departmentDate}}</p> -->
          <span v-if="item.orderStatus == 'OnDelivery'" style="align-self:center; color:#54A93E"></span>
          <span v-if="item.orderStatus == 'HoldDelivery'" style="align-self:center; color:red"></span>
          <span v-if="item.orderStatus == 'UnSettle'" style="align-self:center; color:red"></span>
          <span v-if="item.orderStatus == 'UnDeal'" style="align-self:center; color:red"></span>
          <span v-if="item.orderStatus == 'Refuse'" style="align-self:center; color:red"></span>
          <span v-if="item.orderStatus == 'completed'" style="align-self:center; color:#54A93E"></span>
          <span v-if="item.orderStatus == 'cancelled'" style="align-self:center; color:red"></span>
          <span v-if="item.orderStatus == 'Finish'" style="align-self:center; color:#54A93E"></span>
          <span v-if="item.orderStatus == 'Closed'" style="align-self:center; color:red"></span>
        </div>
        <div class="orderDetail-list__sum">
          <p><span class="fontBold">:&nbsp;&nbsp;</span>{{item.sn}}</p>
          <a @click="handleCheckOrder(item.sn)" style="align-self:center">{{orderDetail}}</a>
        </div>
        <div class="orderDetail-list__promot">
          <p><span class="fontBold">:&nbsp;&nbsp;</span>{{item.memberName}}</p>
        </div>
        <div class="orderDetail-list__cusAmount">
          <p><span class="fontBold">:&nbsp;&nbsp;</span>{{item.memberPhone}}</p>
        </div>
        <div class="orderDetail-list__fees">
          <p><span class="fontBold">:&nbsp;&nbsp;</span>{{item.memberDistrict}}{{item.gaodeAddress}}{{item.memberAddress}}{{item.memberRoom}}</p>
        </div>
        <div class="orderDetail-list__fees">
          <p><span class="fontBold">:&nbsp;&nbsp;</span>{{filterDate(item.createDate)}}</p>
        </div>
        <div class="orderDetail-list__paidAmount">
          <p><span class="fontBold">:&nbsp;&nbsp;</span>{{item.totalPrice}}</p>
        </div>
      </div>
      <div class="orderDetail-list__loadMore">
        <!-- <i class="orderDetail-list__upIcon"></i> -->
        <!-- <span style="font-size:14px"></span> -->
      </div>
        <!-- <btn size="large"></btn>
        <btn size="large"></btn> -->
        <div v-if="this.$route.query.status == 'Unconfirmed'" class="page-bottom">
          <van-button @click="handleComfirm" class="order-footer" type="primary" size="large" square></van-button>
          <van-button @click="handleConfuse" class="order-footer" type="default" size="large" square></van-button>
        </div>
        <van-button v-if="this.$route.query.status == 'Confirmed'" disable class="order-footer__comfirmed" type="default" size="large" square></van-button>
        <van-button v-if="this.$route.query.status == 'Confuse' && this.billLists !==null" disable style="background:#F2F2F2; color:#9999A0; font-weight:bold" class="order-footer__comfuse" type="default" size="large" square></van-button>
    </page-content>
  </div>
</template>

<script>
  import Vue from 'vue'
  import Content from '../components/content'
  import { getMonthDetails, getAllCouriers, changeHandler } from '../api/billDetail.js'
  import Button from '../../node_modules/vant/lib/button';
  import '../../node_modules/vant/lib/button/style';
  import Toast from '../../node_modules/vant/lib/toast';
  import '../../node_modules/vant/lib/toast/style';

  Vue.use(Toast)
  Vue.use(Button);
  export default {
    components: {
      'page-content': Content,
    },
    data() {
      return {
        selected: 1,
        couriers: [],
        orderTotal: 112,
        orderStatus: '',
        orderDetail: '',

        couriers: [],
        billLists: [],
        id: ''
      }
    },
    methods: {
      filterDate (x){
        const regexp=/(?:\.0*|(\.\d+?)0+)$/
        return x.toString().replace(regexp,'$1')
      },
      handleSelect(id) {
        // this.id = id
        console.log(id);
        getMonthDetails(this.$route.query.departmentBillId, id).then(res => {
          console.log(res);
          if (res.data.code == 0) {
            this.billLists = res.data.data.content
          } else {
            this.billLists = null
          }
        })
      },
      handleComfirm() {
        changeHandler(this.$route.query.departmentBillId, 'Confirmed').then(res => {
          Toast.success({message:'',duration:1000});
          this.$router.go(-1)
        })
      },
      handleConfuse() {
        changeHandler(this.$route.query.departmentBillId, 'Confuse').then(res => {
          Toast.success({message:'',duration:1000});
          this.$router.go(-1)
        })
      },
      handleCheckOrder(sn) {
        console.log(sn);
        this.$router.push({path:'/CheckoutOrderDetail',query:{sn:sn}})
      }
    },
    mounted() {
      getMonthDetails(this.$route.query.departmentBillId).then(res => {
        console.log('montsh',res);
        this.billLists = res.data.data.content
        console.log(this.billLists);
      }),
      getAllCouriers().then(res => {
        console.log('getAllCouriers',res);
        this.couriers = res.data.data.serverList
        this.couriers.unshift({name:'',id:''})
      })
    },
  }
  
</script>

<style lang="less">
  .fontBold {
    font-weight: bold;
  }
  .comment-header {
    color: #999999;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #999999;
    padding: 0 .625rem;
  }
</style>

<style lang="less" scoped>
  .comment-courierList {
    margin: 1rem;
    // margin-bottom: 1.5rem;
    font-size: .8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: -.5rem;
  }
  
  .comment-courierList__selectbox {
    // border: .1rem solid gray;
    border-radius: 5px;
    width: 6.25rem;
    height: 1.2rem;
    margin: .8rem 0;
  }
  
  .orderDetail-list__detail {
    margin: 0 1rem;
    font-size: .75rem;
    border-bottom: 1px solid rgb(226, 226, 226);
  }
  
  .orderDetail-list__status,
  .orderDetail-list__total,
  .orderDetail-list__promot,
  .orderDetail-list__cusAmount,
  .orderDetail-list__fees,
  .orderDetail-list__sum,
  .orderDetail-list__paidAmount {
    display: flex;
    justify-content: space-between;
    margin-bottom: -1.2rem;
  }
  
  .orderDetail-list__paidAmount {
    margin-bottom: .5rem;
  }
  .orderDetail-list__loadMore {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .75rem;
    margin-top: .75rem;
    margin-bottom: 1rem
  }
  
  .orderDetail-list__upIcon {
    display: inline-block;
    width: .8rem;
    height: .8rem;
    background-image: url("../assets/images/orderDetail/u678.png");
    background-repeat: no-repeat;
    background-size: 100%;
    position: relative;
    margin-right: .25rem;
    // top: .12rem;
  }
  .order-footer__comfirmed {
    // color: red
    // display: flex;
    // justify-content: space-between;
    // position: absolute;
    // width: 100%;
    // bottom: 0;
    // font-size: 1rem;
  }
  .order-footer__comfuse {
    // position: absolute;
    bottom: 0;
  }
  .page-bottom {
    // display: flex;
  }
</style>



